import React, { lazy, Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
// import { Header, ContextProvider } from 'components';

const Stats = lazy(() => import('./pages/Stats'));
const Facility = lazy(() => import('./pages/Facility'));

// function CustomRoute({ component: Page, path }) {
function CustomRoute({ path }) {
  return (
    <Route
      path={path}
      // render={({ history, match }) => {
      render={() => {
        window.location.replace(`https://dragalialost.info${path}`);
        return null;
        // return (
        //   <ContextProvider history={history} match={match}>
        //     <Header />
        //     <Page />
        //   </ContextProvider>
        // );
      }}
    />
  );
}

function Routes() {
  return (
    <Suspense fallback={<div>Loading</div>}>
      <Switch>
        <CustomRoute path="/stats" component={Stats} />
        <CustomRoute path="/facility" component={Facility} />
        <Redirect to="/stats" />
      </Switch>
    </Suspense>
  );
}

export default Routes;
